.post{
    width: 385px;
    margin: 0 25px 40px 25px;

    img{
        width: 100%;
        height: 280px;
        object-fit: cover;
        border-radius: 7px;
    }
    .info{
        display: flex;
        flex-direction: column;
        align-items: center;
        .cat{
            font-family: 'Varela-Round', sans-serif;
            font-size: 11px;
            color: #be9656;
            line-height: 20px;
            margin-top: 15px;
            margin-right: 10px;
            cursor: pointer;
        }
        .title{
            font-family: 'Josefin Sans', sans-serif;
            font-size: 24px;
            font-weight: 700;
            margin-top: 15px;
            cursor: pointer;
        }
        .date{
            font-family: 'Lora', serif;
            font-style: italic;
            font-size: 13px;
            color: #999;
        }
    }
    .description{
        font-family: 'Varela Round', sans-serif;
        font-size: 14px;
        color: #444;
        line-height: 24px;
        margin-top: 15px;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
    }
}