.login{
    height: calc(100vh - 50px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(rgba(255,255,255,0.5),rgba(255,255,255,0.5)), url("https://images.pexels.com/photos/768473/pexels-photo-768473.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500");
    background-size: cover;
    
    .title{
        font-family: "Lora", sans-serif;
        font-size: 50px;
        font-weight: 200;
    }
    form{
        margin-top: 20px;
        display: flex;
        flex-direction: column;
        label{
            margin: 10px 0;
        }
        input{
            padding: 10px;
            background-color: white;
            border: none;
        }
        .loginBtn{
            margin-top: 20px;
            cursor: pointer;
            background-color: lightcoral;
            border: none;
            border-radius: 10px;
            padding: 10px;
            color: white;
            &:disabled{
                cursor: not-allowed;
                background-color: rgb(242, 168, 168);
            }
        }
        .loginRegisterBtn{
            position: absolute;
            top: 60px;
            right: 20px;
            cursor: pointer;
            background-color: teal;
            border: none;
            border-radius: 10px;
            padding: 10px;
            color: white;
        }
    }
}