.settings{
    display: flex;
    .wrapper{
        flex: 9;
        padding: 20px;
        .title{
            display: flex;
            align-items: center;
            justify-content: space-between;

        }
        .updateTitle{
            font-size: 30px;
            margin-bottom: 20px;
            color: lightcoral;
        }
        .deleteTitle{
            font-size: 12px;
            color: red;
            cursor: pointer;
        }
        .form{
            display: flex;
            flex-direction: column;
            
            .dp{
                display: flex;
                align-items: center;
                margin: 10px 0; 
    
                img{
                    width: 70px;
                    height: 70px;
                    border-radius: 20px;
                    object-fit: cover;
                }
                i{
                    background-color: lightcoral;
                    color: white;
                    width: 25px;
                    height: 25px;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin-left: 10px;
                    cursor: pointer;
                }
            }
            label{
                font-size: 20px;
                margin-top: 20px;
            }
            input{
                color: gray;
                margin: 10px 0;
                height: 30px;
                border: none;
                border-bottom: 1px solid lightgray;
            }
            .submit{
                width: 150px;
                border: none;
                border-radius: 10px;
                padding: 10px;
                margin-top: 20px;
                background-color: teal;
                color: white;
                align-self: center;
                cursor: pointer;
            }
        }
    }
}