.sidebar{
    flex: 3;
    margin: 20px;
    padding-bottom: 30px;
    background-color: #fdfbfb;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;

    .item{
        display: flex;
        flex-direction: column;
        align-items: center;
        .title{
            margin: 10px;
            padding: 5px;
            width: 80%;
            border-top: 1px solid #a7a4a4;
            border-bottom: 1px solid #a7a4a4;
            font-family: 'Varela Round', sans-serif;
            font-size: 12px;
            color:#222;
            font-weight: 600;
            line-height: 20px;
            text-align: center;
        }
        img{
            margin-top: 15px;
        }
        p{
            padding: 30px;
        }
        .list{
            list-style: none;
            margin-bottom: 30px;

            .listItem{
                display: inline-block;
                width: 50%;
                margin-top: 15px;
                cursor: pointer;
            }
        }
        .social{
            margin-top: 15px;
            i{
                font-size: 16px;
                margin: 0 5px;
                cursor: pointer;
            }
        }
    }
    
}
